body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main-container
{
  display: flex;
  flex-direction: column;
}

.fade-in 
{
  display: flex;
  flex-direction: column;
  -webkit-animation: fadeIn 5s;
          animation: fadeIn 5s;
  justify-content: center;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/*GENERAL BACKGROUND*/
body
{
  background: -webkit-linear-gradient(rgba(0,0,0,0.95), rgba(0,0,0,0.95)), no-repeat center;
  background: linear-gradient(rgba(0,0,0,0.95), rgba(0,0,0,0.95)), no-repeat center;
}
.homepage
{
  height: 100vh;
}

.profile
{
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  width: 100%;
  align-items: center;
  background: -webkit-linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url(/static/media/profilebackground.e6dd8d77.jpg) no-repeat;
  background: linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url(/static/media/profilebackground.e6dd8d77.jpg) no-repeat;
  background-size: cover;
  background-position: center;
}

.profile-photo
{
  margin-top: 90px;
}

.profile-photo img
{
  max-height: 190px;
  max-width:  190px;
}

.profile-title h1
{
  text-align: center;
  margin-top: 30px;
  width: 100%;
  font-family: "Courier New", Courier, monospace;
  color:rgb(255, 255, 255);
  font-size: 37px;
  font-weight: bold;
  text-overflow: ellipsis;
}

body {
  font-family: "Courier New", Courier, monospace;
}

#cursor {
  border-left: .45em solid rgb(202, 202, 202);
  -webkit-animation: blink 0.9s steps(1) infinite;
          animation: blink 0.9s steps(1) infinite;
}

@-webkit-keyframes blink {
  50% {
    border-color: transparent;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}

.main-nav
{
  display: flex;
  flex-direction: column;
  margin-top: 30px;
}

.main-nav ul
{
  padding: 0;
}

.nav-menu 
{
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.home-nav-menu li a
{
  color: white;
  text-decoration: none;
  font-family: "Courier New", Courier, monospace;
  font-size: 21px;
  font-weight: bold;
  margin: 95px;
  overflow: hidden;
  -webkit-transition: 0.5s all ease;
  transition: 0.5s all ease;
}

.home-nav-menu li a:hover
{
  color: rgb(85, 85, 85);
}

.nav-media
{
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 75px;
}

.nav-media li a
{
  margin: 25px;
  color: white;  
}

.nav-media li i:hover
{
  color: rgb(85, 85, 85);
  -webkit-transition: 0.5s all ease;
  transition: 0.5s all ease;
}

@media only screen and (max-width: 750px)
{
  .home-nav-menu li a
  {
    margin: 30px;
  }
}

@media only screen and (min-height: 800px)
{
  .nav-media
  {
    margin-top: 300px;
  }
}
.about-section
{
  margin-top: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}

.about-header
{
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 130px;
  padding-bottom: 130px;
  align-items: center;
  background: -webkit-linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url(/static/media/about1.5ef1fcd2.JPG) no-repeat;
  background: linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url(/static/media/about1.5ef1fcd2.JPG) no-repeat;
  background-size: cover;
  background-position: center;
}

.about-header h1
{
  color: rgb(252, 142, 16);
  text-align: center;
  font-family: "Courier New", Courier, monospace;
  font-size: 50px;
  font-weight: bold;
}

.description
{
  color: white;
  width: 70%;
  font-family: "Courier New", Courier, monospace;
}

.description p
{
  font-weight: bold;
  margin-top: 60px;
  font-size: 21px;
  text-align: justify;
  line-height: 1.4em;
}

.cat-highlight
{
  color:rgb(252, 142, 16);
}

@media only screen and (min-width: 1650px)
{
  .about-header
  {
    padding-top: 175px;
    padding-bottom: 175px;
  }
}

@media only screen and (max-width: 750px)
{
  .about-header
  {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .description
  {
    color: white;
    width: 90%;
    font-family: "Courier New", Courier, monospace;
  }
}
.photos-section
{
  height: 100vh;
  margin-bottom: 200px;
}

.photos-header h1
{
    color: white;
    text-align: center;
    font-family: "Courier New", Courier, monospace;
    font-size: 45px;
    font-weight: bold;
}

.photos img
{
  max-width: 100%;
  height: auto;
}

.photos
{
  display: flex;
  flex-wrap: wrap;
  margin: 20px 150px;
}

.column 
{
  flex: 25% 1;
  max-width: 30%;
  margin-left: auto;
  margin-right: auto;
}
 
@media screen and (max-width: 900px) {
  .photos {
    margin: 20px 20px;
  }
}

.box
{
  position: relative; 
  margin-top: 10px;
}

.box:hover img
{
  opacity: 0.3;
  -webkit-transition: 0.5s all ease;
  transition: 0.5s all ease;
}

.box:hover .detail
{
  opacity: 0;
}

.column img {
  margin-top: 8px;
  vertical-align: middle;
}

.photos-link
{
  margin-top: 100px;
  font-family: "Courier New", Courier, monospace;
  font-size: 20px;
  font-weight: bold;
  color: white;
  text-align: center;
}
.projects-header h1
{
    color: white;
    text-align: center;
    font-family: "Courier New", Courier, monospace;
    font-size: 45px;
    font-weight: bold;
}

.projects-section ul
{
    padding: 0;
}

.projects-list
{
    margin-top: 30px;
}

.project
{
    margin-top: 4px;
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    width: 100%;
    align-items: center;
    background: rgb(39, 39, 39);
    padding-top: 30px;
    padding-bottom: 30px;
}

.project-photo img
{
    max-height: 165px;
    max-width:  165px;
}

.project-photo2 img
{
    height: 165px;
    width:  165px;
}

.projects-list h2
{
    font-family: "Courier New", Courier, monospace;
    font-size: 33px;
    color: rgb(252, 142, 16);
    font-weight: bold;
    margin: 8px;
}

.proj-highlight
{
    color: rgb(255, 255, 255);
    font-weight: bold;
    font-size: 17px;
    text-decoration: underline;
    font-style: oblique;
    font-family: serif;
}

.skills
{
    display: flex;
    font-family: "Courier New", Courier, monospace;
    flex-direction: row;
    justify-content: center;
}

.skills li
{
    color: rgb(170, 170, 170);
    font-size: 14px;
    font-weight: bold;
    text-decoration: none;
    border: 2px solid rgb(170, 170, 170);
    border-radius: 9px;
    padding: 5px 16px;
    margin: 5px;
    display: flex;
}

.project-description
{
    margin-top: 25px;
    color: white;
    width: 40%;
    font-family: "Courier New", Courier, monospace;
}

.project-description p
{
    font-weight: bold;
    font-size: 19px;
    text-align: justify;
    line-height: 1.2em;
    margin-bottom: 6px;
}

.github-link
{
    margin-top: 10px;
}

.github-link a
{
    color: white;
    -webkit-transition: 0.5s all ease;
    transition: 0.5s all ease;
}

.github-link a:hover
{
    color: rgb(85, 85, 85);
}

.demo-link
{
    margin-top: 15px;
}

.demo-link a
{
    color: white;
    -webkit-transition: 0.5s all ease;
    transition: 0.5s all ease;
}

.demo-link a:hover
{
    color: rgb(85, 85, 85);
}

@media only screen and (max-width: 750px)
{
    .project-description
    {
        width: 70%;
    }

    .proj-highlight
    {
        font-size: 13px;
    }
}
